import m from "./_snowpack/pkg/mithril.js"
import Layout from "./view/layout.js"
import Discussion from "./view/pages/discussion.js"
import Introduction from "./view/pages/intro.js"
import MC from "./view/pages/mc.js"
import {PartnershipsView} from "./view/pages/partnerships.js"
import PilotsView from "./view/pages/pilots.js"
import Ranking from "./view/pages/ranking.js"
import RankingTwo from "./view/pages/ranking2.js"

const AppRouter = ()=>{
    m.route(document.body,'/',{
        '/':{
            view:()=>{
               return m(Layout, m(Introduction))
            }
        },
        '/ranking1':{
            view:()=>{
               return m(Layout, m(Ranking))
            }
        },
        '/ranking2':{
            view:()=>{
               return m(Layout, m(RankingTwo))
            }
        },
        '/mc1':{
            view:()=>{
                return m(Layout, m(MC))
            }
        },
        '/partnership':{
            view:()=>{
                return m(Layout, m(PartnershipsView))
            }
        },
        '/pilots':{
            view:(vnode)=>{
                
                return m(Layout, m(PilotsView, {partner:m.route.param("partner")}))
            }
        },
        '/discussion':{
            view:()=>{
               return m(Layout, m(Discussion))
            }
        },
    })
}

AppRouter();